<template>
  <header>
    <div class="top-header">
      <span class="top-logo fl" @click="toIndex">
        <span class="iconfont logo-icon" :class="iconfont"></span>
        <el-divider direction="vertical"></el-divider>
        <span class="logo-text">{{ PlatformName }}</span>
      </span>
      <span class="top-btns fr">
        <span v-if="isLogin" class="person-btn" @click="toPerson"
          >个人中心</span
        >
        <span v-if="isLogin" class="quit-btn" @click="quitLogin">退出</span>
        <span v-if="!isLogin" class="login-btn" @click="quitLogin">登录</span>
        <span v-if="!isLogin" class="register-btn" @click="register">注册</span>
      </span>
      <el-input
        class="top-search fr"
        size="small"
        placeholder="请输入内容"
        suffix-icon="el-icon-search"
        v-model="search"
        @keyup.native="gotosearch($event)"
      ></el-input>
      <span class="clear"></span>
    </div>
    <div class="top-navs" v-if="isMain">
      <div class="nav-content">
        <router-link
          v-for="(item, index) in navList"
          :key="index"
          :to="item.path"
          active-class="nav-visted"
          >{{ item.title }}</router-link
        >
      </div>
    </div>
  </header>
</template>
<script>
import http from "../utils/http.js";
import cookie from "../utils/cookieOperate.js";
import api from "../utils/api.js";
// import cookieOperate from '../utils/cookieOperate.js';
export default {
  data() {
    return {
      iconfont: "icon-FJRH",
      Neutral: false,
      PlatformName: "睿课智慧资源学习平台",
      isMain: true,
      isLogin: false,
      activeName: "index",
      search: "",
      navList: [
        {
          title: "首页",
          path: "/main/index"
        },
        // {
        //   title: "互动课堂",
        //   path: "/main/interactive"
        // },
        {
          title: "课堂实录",
          path: "/main/course"
        },
        {
          title: "课堂直播",
          path: "/main/live"
        }
        // {
        //   title: "慕课资源",
        //   path: "/main/resource"
        // },
        // {
        //   title: "微课园地",
        //   path: "/main/field"
        // },
        // {
        //   title: "名师讲堂",
        //   path: "/main/teacher"
        // }
      ]
    };
  },
  async created() {
    let srvConfig = await this.$root.GetSrvConfig();
    this.Neutral = srvConfig.Neutral;
    this.PlatformName =
      (this.Neutral && this.Neutral == true ? "" : "睿课") + "智慧资源学习平台";

    this.iconfont =
      this.Neutral && this.Neutral == true ? "icon-shu2" : "icon-FJRH";

    this.activeName = this.$route.path.split("/")[2];
    this.statusHandler();
    // if (this.activeName != "detail")
    //   this.$router.push({
    //     path: this.$router.path,
    //     query: {}
    //   });
  },
  watch: {
    $route(to, from) {
      this.activeName = to.path.split("/")[2];
      this.statusHandler();
    }
  },
  methods: {
    statusHandler() {
      this.isLogin = http.logined();
    },
    toIndex() {
      this.$router.push("/main/index");
    },
    register() {},
    quitLogin() {
      cookie.delete(api.cookie.token);
      cookie.delete(api.cookie.user_infor);
      cookie.delete(api.cookie.video_config);
      var logintype = cookie.get("loginType");
      if (logintype == "1") {
        if (this.isLogin == true) {
          if (window.localStorage.getItem("UnifiedloginOutUrl") != "") {
            window.location.href = window.localStorage.getItem(
              "UnifiedloginOutUrl"
            );
          }
        }
      } else {
        this.isLogin = false;
        setTimeout(() => {
          this.$router.push("/main/login");
        }, 125);
      }
      cookie.delete("loginType");
    },
    toPerson() {
      let userInfo = http.getUser();
      if (userInfo) {
        if (userInfo.UserType == 1)
          // 教师
          this.$router.push("/main/teacherSpace");
        //学生
        else this.$router.push("/main/studentSpace/task/list");
      }
    },
    gotosearch(event) {
      if (event.keyCode === 13) {
        if (
          this.activeName == "course" ||
          this.activeName == "live" ||
          this.activeName == "index"
        ) {
          let paramlist = [];
          paramlist.push({
            title: "搜索",
            id: "",
            text: this.search
          });
          let param = JSON.stringify(paramlist);

          this.$router.push({
            name: this.activeName,
            query: {
              selectData: param,
              sfield: "",
              sdir: 1
            }
          });
        }
      }
    }
  }
};
</script>
<style lang="less">
header {
  width: 100%;
  background: #fff;
  user-select: none;
  .top-header {
    width: 1200px;
    height: 80px;
    line-height: 80px;
    margin: 0 auto;
    .top-logo {
      height: 100%;
      color: rgb(63, 149, 236);
      cursor: pointer;
      span {
        vertical-align: middle;
      }
      .logo-icon {
        font-size: 4.5rem !important;
      }
      .logo-text {
        font-weight: 888;
        font-size: 2.33rem;
        letter-spacing: 1.5px;
        // font-family: "STxk";
      }
      .el-divider {
        height: 45px;
        margin: auto 10px;
      }
    }
    .top-search {
      width: 260px;
      margin-right: 52px;
      input {
        border-radius: 20px;
      }
    }
    .top-btns {
      width: 100px;
      color: #aaa;
      font-size: 0.8rem;
      display: flex;
      justify-content: space-between;
      span:hover {
        color: #0080ff;
        cursor: pointer;
      }
      .login-btn,
      .person-btn {
        color: #409eff;
      }
    }
  }
  .top-navs {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #2a9efc;
    .nav-content {
      width: 1200px;
      margin: 0 auto;
      text-align: left;
      a {
        width: 96px;
        color: #a2d5fe;
        display: inline-block;
        text-align: center;
        text-decoration: none;
      }
      .nav-visted {
        color: #fff;
      }
    }
  }
}
</style>



