<template>
  <div>
    <top-bar></top-bar>
    <router-view v-if="!isPerson"></router-view>
    <transition
      v-if="isPerson"
      enter-active-class="zoomIn"
      leave-active-class="zoomOut"
    >
      <router-view class="animated fast"></router-view>
    </transition>
  </div>
</template>
<script>
import topBar from "../components/topBar";
import http from "../utils/http";
export default {
  components: {
    topBar
  },
  data() {
    return {
      isPerson: false
    };
  },
  watch: {
    $route(to, from) {
      let toPath = to.path.split("/")[2];
      /* eslint-disable */
      if (toPath != "index") {
        // if(!http.logined()) {
        //   this.$router.push("/main/login");
        // }
      }
      let fromPath = from.path.split("/")[2];
      if (
        toPath === "teacherSpace" ||
        toPath === "studentSpace" ||
        toPath === "login" ||
        toPath === "binding" ||
        fromPath === "teacherSpace" ||
        fromPath === "studentSpace" ||
        fromPath === "login" ||
        fromPath === "binding"
      ) {
        this.isPerson = true;
      } else {
        this.isPerson = false;
      }
    }
  }
};
</script>
<style lang="less">
@import "animate.css";
.fadeOutLeft,
.fadeOutRight,
.zoomOut {
  height: 0;
}
.course-content {
  width: 1200px;
  margin: 0 auto;
  margin-top: 25px;
  min-height: calc(100vh - 120px - 200px);
  .courses-box {
    margin-top: 25px;
  }
}
.course-page {
  width: 100%;
  height: 70px;
  text-align: center;
  clear: both;
  padding-top: 18px;
  .el-pagination button,
  .el-pagination li {
    padding: 6px 12px;
    border: 1px solid #dddddd;
    position: relative;
    height: 32px;
    line-height: 19px;
    margin-left: -1px;
  }
  .el-pagination span:not([class*="suffix"]) {
    vertical-align: bottom !important;
  }
  .el-pager li.active + li {
    border-left: 1px solid #dddddd !important;
  }
}
.course-detail .el-tabs__nav-scroll {
  padding-left: 1.52rem !important;
}
.el-rate__icon {
  font-size: 20px !important;
}
.el-rate__text {
  font-size: 18px !important;
  color: #ff9900 !important;
}
</style>



